import React from "react"
import styled from "styled-components"
import SkillItem from "./SkillItem"
import { DiJavascript1 } from "react-icons/di"
import { DiModernizr } from "react-icons/di"
import { DiCodepen } from "react-icons/di"

import { DiCss3 } from "react-icons/di"
import { DiDatabase } from "react-icons/di"
import { DiExtjs } from "react-icons/di"
import { DiGit } from "react-icons/di"
import { DiHtml5 } from "react-icons/di"

import { DiGoogleAnalytics } from "react-icons/di"
import { DiGulp } from "react-icons/di"
import { DiHeroku } from "react-icons/di"
import { DiJira } from "react-icons/di"
import { DiLinux } from "react-icons/di"

import { DiMarkdown } from "react-icons/di"
import { DiMaterializecss } from "react-icons/di"
import { DiMysql } from "react-icons/di"
import { DiNodejs } from "react-icons/di"


import { DiNpm } from "react-icons/di"
import { DiReact } from "react-icons/di"
import { DiSass } from "react-icons/di"
import { DiTerminal } from "react-icons/di"
import { DiW3C } from "react-icons/di"

import { DiBootstrap } from "react-icons/di"
import { DiChrome } from "react-icons/di"

const Skills = styled.section`
margin-top:2rem;
margin-bottom:2rem;
    display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  grid-gap: 0.5rem;


&> div {
  
  padding: 1rem;
  display: grid;
  place-items: center;
}

& > div::before {
  content: "";
  padding-bottom: 100%;
  display: block;
}

& > div::before,
& > div > svg {
color:#EEE8A9;
  grid-area: 1 / 1 / 2 / 2;
}

& > div > svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}


`

export const SkillGrid = () => {

  return (
    <Skills>
      <SkillItem item={<DiJavascript1/>} description="JavaScript"/>
      <SkillItem item={<DiReact/>} description="ReactJS"/>
      <SkillItem item={<DiNpm/>} description="npm"/>
      <SkillItem item={<DiCss3/>} description="CSS3"/>
      <SkillItem item={<DiHtml5/>} description="HTML5"/>
      <SkillItem item={<DiSass/>} description="SASS"/>
      <SkillItem item={<DiChrome/>} description="Chrome"/>
      <SkillItem item={<DiNodejs/>} description="NodeJS"/>
      <SkillItem item={<DiW3C/>} description="W3C"/>
      <SkillItem item={<DiGoogleAnalytics/>} description="Google Analytics"/>
      <SkillItem item={<DiDatabase/>} description="Базы данных"/>
      <SkillItem item={<DiGit/>} description="git"/>
      <SkillItem item={<DiBootstrap/>} description="Bootstrap"/>
      <SkillItem item={<DiTerminal/>} description="Консоль"/>
      <SkillItem item={<DiLinux/>} description="Linux"/>
      <SkillItem item={<DiMysql/>} description="MySQL"/>
      <SkillItem item={<DiMaterializecss/>} description="Materialize"/>
      <SkillItem item={<DiExtjs/>} description="ExtJS"/>
      <SkillItem item={<DiJira/>} description="Jira"/>
      <SkillItem item={<DiGulp/>} description="Gulp"/>
      <SkillItem item={<DiMarkdown/>} description="Markdown"/>
      <SkillItem item={<DiHeroku/>} description="Heroku"/>
      <SkillItem item={<DiModernizr/>} description="Modernizr"/>
      <SkillItem item={<DiCodepen/>} description="Codepen"/>
    </Skills>

  )
}

