import React from "react"
import styled from "styled-components"

export const TimelineItem = styled.div`
width:100%
item {
  display: flex;
  flex: auto;
  overflow-y: auto;
  padding: 0rem 1rem 0rem 1rem;
  width:100%;
}

#timeline {
  position: relative;
  display: table; //*For Firefox
  height: 100%;
  width:100%;
  margin: {
    left: auto;
    right: auto;
    top: 5rem;
  }
  div {
    &:after {
      content: '';
      width: 2px;
      position: absolute;
      top: .5rem;
      bottom: 0rem;
      left: 60px;
      z-index: 1;
      background: #C5C5C5;
    }
  }
  h3 {
    position: -webkit-sticky;
    position: sticky;
    top: 5rem;
    color: #888;
    margin: 0;
    font: {
      size: 1em;
      weight: 400;
    }
    @media (min-width: 62em) {
      font-size: 1.1em;
    }
  }
  section.year {
  position: relative;
  &:first-child section{
    margin-top: -1.3em;
    padding-bottom: 0px;
  }
  section {
    position: relative;
    padding-bottom: 1.25em;
    margin-bottom: 2.2em;
    h4 {
      position: absolute;
      top: 0;
      font: {
        size: .9em;
        weight: 400;
      }
      line-height: 1.2em;
      margin: 0;
      padding: 0 0 0 89px;
      color: #C5C5C5;
      @media (min-width: 62em) {
        font-size: 1em;
      }
    }
    ul {
           @media (max-width: 600px) {
         width:80vw;
      }
      list-style-type: none;
      padding: 1.2rem 0 0 75px;
      margin: -1.35rem 0 1em;
      // max-width: 32rem;
      font-size: 1em;
      @media (min-width: 62em) {
        font-size: 1.1em;
        padding: 1.2rem 0 0 81px;
      }
      &:last-child {
        margin: {
          bottom: 0;
        }
      }
      &:first-of-type:after {
        content: '';
        width: 15px;
        height: 15px;
        background: #C5C5C5;
        border: 2px solid #FFFFFF;
        border-radius: 50%;
        position: absolute;
        left: 54px;
        top: 3px;
        z-index: 2;
      }
      li {
  
        margin-left: .5rem;
        &:before {
          content: '-';
          margin-left: -.5rem;
          padding-right: .3rem;
        }
        &:not(:first-child) {
          margin-top: .5rem;
        }
        span.price {
          color: mediumturquoise;
          font-weight: 500;
        }
      }
    }
  }
}
}

#price {
  display: inline;
}

svg {
  border: 3px solid white;
  border-radius: 50%;
  box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
}

.timeline-inner{
width:100%;}
`

const Timeline = (props) => {
  const {item,description} = props;
  return (
    <TimelineItem>
      <div id="timeline">
        <div className="timeline-inner">
          <section className="year">
            <h3>2012</h3>
            <section>
              <h4>Сентябрь</h4>
              <ul>
                <li> Стал студентом Санкт-Петербургского государственного университета аэокосмического приборостроения. Специальность: информатика и вычислительная техника
                </li>
              </ul>
            </section>
          </section>
          <section className="year">
            <h3>2016</h3>
            <section>
              <h4>Июнь</h4>
              <ul>
                <li>Получил звание бакалавра информатики и вычислительной техники</li>
              </ul>
            </section>
            <section>
              <h4>Август</h4>
              <ul>
                <li>Стал магистрантом Санкт-Петербургского государственного университета аэокосмического приборостроения. Специальность: информатика и вычислительная техника. Направление: математическое моделирование.</li>
              </ul>
            </section>
          </section>
          <section className="year">
            <h3>2017</h3>
            <section>
              <h4>Март</h4>
              <ul>
                <li>Стал сотрудником IT Media Group. Технологический стек: php, javascript, html, css. Основные задачи: доработка функционала и разработка новых сайтов с использованием CMS.</li>
              </ul>
            </section>
            <section>
              <h4>Июль</h4>
              <ul>
                <li>Стал сотрудником SpaceWeb. Технологический стек:php, js, html, css</li>
              </ul>
            </section>
          </section>
          <section className="year">
            <h3>2018</h3>
            <section>
              
              <ul>
                <li>Получил звание бакалавра информатики и вычислительной техники</li>
                <li>Стал сотрудником НПК "Интеллектуальные технологии".Технологический стек:js,html,css,nodejs,ReactJS,InfluxDB. Разработка клиентской части системы мониторинга нефтяных местоождений.</li>
              </ul>
              <h4>Июль</h4>
            </section>
            <section>
              <h4>Декабрь</h4>
              <ul>
                <li>Стал сотрудником Premium Betting Technologies.  Технологический стек:ReactJS,JS,SASS,HTML,WebSocket,StompJS,Redux, Jest, nodejs. Разработка клиентской части системы B2b решения для букмекерского бизнеса и ряда сопутствующих проектов.</li>
              </ul>
            </section>
          </section>
        </div>
      </div>
    </TimelineItem>


  )
}

export default Timeline
