import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {SkillGrid} from "../components/BIo/SkillGrid"
import { rhythm } from "../utils/typography"
import Image from "gatsby-image"
import Timeline from "../components/Timeline"

const NonStretchedImage = props => {
  let normalizedProps = props
  if (props.fluid && props.fluid.presentationWidth) {
    normalizedProps = {
      ...props,
      style: {
        ...(props.style || {}),
        maxWidth: props.fluid.presentationWidth,
        margin: "0 auto", // Used to center the image
      },
    }
  }

  return <Image {...normalizedProps} />
}

const About = ({ data, location }) => {
  const {title, author} =  data.site.siteMetadata;

  return (
    <Layout location={location} title={title}>
      <SEO title="Bio info" />
      <h2>Об авторе</h2>
      <p>
        Здравствуйте! Меня зовут Дмитрий. Я живу и работаю в Санкт-Петербурге. На данный момент я являюсь частью команды разработчиков в Premium Betting Technologies.
      </p>

      <NonStretchedImage
        fluid={data.avatar.childImageSharp.fluid}
        alt={author.name}
        style={{
          marginRight: rhythm(1 / 2),
          marginBottom: 0,
          minWidth: 100,
          // borderRadius: `100%`,
        }}/>
      <p> Далее представлены основные технологии, языки, библиотеки,  framework'и, которые я использую в своих проектах: </p>
      <SkillGrid/>
      <h3>Карьера и образование</h3>
      <Timeline/>
    </Layout>
  )
}

export default About

export const pageQuery = graphql`
    query {
        site {
            siteMetadata {
                title,
                author {
                    name
                    summary
                }
            }
        },
        avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
            childImageSharp {
                fluid(maxWidth:300) {
                    ...GatsbyImageSharpFluid
                    presentationWidth
                }
            }
        }
    }
`